/*------------------------------------*\
    $IMAGES
\*------------------------------------*/
img {
    max-width: 100%;
}
.circular {
    border-radius: 50%;
    width: 100%;
    height: 100%;
    max-width: 100px;
}

.avatar {
    @include equal-radius;
    border: 1px solid rgba(0,0,0,.1);
}

.deployment-logo {
    height: 80px;
    width: 80px;
    @include equal-radius(100%);
}
